<template>
  <div id="homepage">



    <v-btn class="ma-2" outlined large dark color="indigo" @click="pairWallet">
      Wallet Connect
    </v-btn>

    <div id="accountId">
      Account Id: <span class="bold">{{accountId}}</span>
    </div>

    <br/><br/><br/><br/>
    <!-- Verify wallet -->
    <v-btn class="ma-2" outlined x-large dark color="indigo" @click="verify">
      Verify
    </v-btn>

  </div>
</template>

<script>
  import { HashConnect } from 'hashconnect';
  import axios from 'axios';

  export default {
    name: 'HomePage',
    data: () => {
      return {
        accountId: "---"
      }
    },
    methods: {
      async pairWallet() {
        let hashconnect = new HashConnect();

        let appMetadata = {
            name: "dApp Example",
            description: "An example hedera dApp",
            icon: "https://absolute.url/to/icon.png"
        }

        let initData = await hashconnect.init(appMetadata, "mainnet", false);

        console.log("PAIRING...")

        hashconnect.foundExtensionEvent.once((walletMetadata) => {
            console.log(walletMetadata)
            hashconnect.connectToLocalWallet(initData.pairingString, walletMetadata);
        })

        hashconnect.pairingEvent.once((pairingData) => {
            console.log('wallet paired xxx')
            console.log(pairingData)

            this.accountId = pairingData.accountIds[0];
        })
      },
      verify() {
        let url = `/api`;

        const urlParams = new URLSearchParams(window.location.search);
        const code = urlParams.get('code')

        let body = JSON.stringify({
          uid: code,
          wallet_address: this.accountId,
        });

        var authOptions = {
          method: "post",
          url: url,
          data: body,
          headers: {
            "Content-Type": "application/json",
          },
          json: true,
        };

        axios(authOptions).then((resp) => {
          console.log("response :- ",resp);
          alert("Successfully verified!")
        })
        .catch((error) => {
          console.log(error)
        });
      }
    },
    setup() {
      
    }
  }
</script>

